*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  font-style: normal;
  line-height: 167%;
  color: #484848;
  font-family: "FiraSans", "Arial", "sans-serif";
  font-weight: 400;
  font-size: 15px;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%; }

* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }

body {
  min-height: 100vh;
  overflow: auto;
  background-color: #ffffff; }

img,
video,
svg {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

/* :focus {
  outline: none;
} */
input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"], [type="number"], [type="password"], [type="search"], [type="tel"], [type="text"], [type="url"]) {
  appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }
